/* eslint-disable */
import {
  Avatar,
  Box,
  Button,
  CircularProgress,
  Dialog,
  Grid,
  Link,
  Slide,
  Stack,
  TextField,
  Typography,
} from "@mui/material";
import React, { useEffect, useRef, useState } from "react";
import KeyboardArrowDownIcon from "@mui/icons-material/KeyboardArrowDown";
import { TransitionProps } from "@mui/material/transitions";
import Images from "../../assets";
import {
  AVAcommunicationMessage,
  communicationMessage,
  fetchAllMessages,
  seenMessageDetails,
} from "../../services";
import { useAppDispatch, useAppSelector } from "../../assets/hooks";
import { addMessage, initMessages } from "../../store/Slices/messagesSlice";
import {
  Chat,
  Event,
  Message,
  Participant,
  Sharedinfo,
  TriggerObject,
} from "../../types/types";
import { updateChat } from "../../store/Slices/chatsSlice";
import Socket from "../../assets/socket";
import { normalizeType, setWordCase } from "../../assets/library";
import { Close } from "@mui/icons-material";
import ContentRenderer from "./Content";

const Transition1 = React.forwardRef(function Transition(
  props: TransitionProps & {
    children: React.ReactElement<any, any>;
  },
  ref: React.Ref<unknown>
) {
  return <Slide direction="up" ref={ref} {...props} />;
});

interface props {
  open: boolean;
  setOpen: Function;
  id: any;
  type: "advisors" | "community";
}

const VirtualChatContainer = ({ open, setOpen, id, type }: props) => {
  const profileInfo = useAppSelector((state) => state.sharedInfo);
  const list = useRef<Message[]>([]);
  const [loading, setLoading] = useState(false);
  const messages = useAppSelector((state) => state.messages);
  const [uploadFiles, setUploadFiles] = useState<File[]>([]);
  const [repliedTo, setRepliedTo] = useState(null);
  const [replyDiv, setReplyDiv] = useState(false);
  const [replyDivData, setReplyDivData] = useState("");
  const [typing, setTyping] = useState("");
  const chat = useAppSelector((state) => {
    let currentChats = state.chats;
    return type === "advisors"
      ? currentChats.data.advisors.find((chat) => chat._id === id)
      : currentChats.data.community.find((chat) => chat._id === id);
  });
  const dispatch = useAppDispatch();

  const participantDetails = chat?.participants.find(
    (user) => user._id !== profileInfo?.data?._id
  );
  const seenMessages = async () => {
    const response = await seenMessageDetails(id);
    // console.log(response)
    if (response.success) {
      dispatch(updateChat(response.data));
      if (
        profileInfo.data &&
        profileInfo.data._id &&
        Object.values(profileInfo.data).every(
          (item) => typeof item !== undefined
        )
      ) {
        let triggerObj: TriggerObject = {
          action: "seen",
          sender: { ...profileInfo.data, userType: "student" },
          recievers: chat?.participants.filter(
            (participant: Participant) =>
              participant._id !== profileInfo.data?._id
          ),
          data: response.data,
        };
        Socket.emit("trigger", triggerObj);
      }
    }
  };

  const triggerMessages = (triggerObject: TriggerObject) => {
    switch (triggerObject.action) {
      case "send":
        dispatch(addMessage({ ...triggerObject.data.message, type: "normal" }));
        seenMessages();
        // console.log("message send trigger", triggerObject.data.message);

        break;

      case "typing":
        //triggerObject.data=="start"?dispatch(startTypingMessage({...triggerObject.sender,activity:'typing'})):dispatch(stopTypingMessage({...triggerObject.sender,activity:"inchat"}))
        break;

      case "seen":
        //dispatch(seenMessage({...triggerObject.sender,activity:"inchat"}))
        break;
    }
  };

  const fetchMessage = async () => {
    try {
      setLoading(true);
      const response = await fetchAllMessages(id);
      if (response) {
        let msgs = response.data.map((msg: Message) => ({
          ...msg,
          type: "normal",
        }));
        msgs.reverse();
        dispatch(
          initMessages({
            requestStatus: "initiated",
            responseStatus: "recieved",
            haveAnIssue: false,
            issue: "",
            data: msgs,
          })
        );
        if (
          chat?.unSeenMessages
            .filter(
              (msg: any) => msg.message.sender._id !== profileInfo.data?._id
            )
            .filter(
              (recievedMessage: any) =>
                !recievedMessage.seen.find(
                  (id: string) => id === profileInfo.data?._id
                )
            ).length != 0
        ) {
          await seenMessages();
        }
      }
      setLoading(false);
    } catch (error) {
      setLoading(false);
    }
  };

  const bakeMessages = (messages: Message[], chat: Chat, user: Sharedinfo) => {
    let unsetParticipants = chat.participants.filter(
      (chat) => chat._id != user._id
    );
    let list: Message[] = [];
    let seenParticipants: Participant[] = [];
    let unseenParticipants: Participant[] = [];
    messages.reverse();
    messages.forEach((msg) => {
      if (msg.sender?._id == user._id) {
        if (unsetParticipants.length != 0) {
          if (chat.unSeenMessages.length == 0) {
            if (unsetParticipants.length != 0) {
              seenParticipants = [...unsetParticipants];
            }
          } else {
            if (
              !chat.unSeenMessages.find(
                (unseenMsg) => unseenMsg.message._id == msg._id
              )
            ) {
              seenParticipants = [...unsetParticipants];
            } else {
              seenParticipants = unsetParticipants.filter((unsetParticipant) =>
                chat.unSeenMessages
                  .find((unseenMsg) => unseenMsg.message._id == msg._id)
                  ?.seen.find(
                    (seenParticipant) => seenParticipant == unsetParticipant._id
                  )
              );
            }
          }
          if (seenParticipants.length != 0) {
            let seenMessage = seenParticipants.reduce(
              (message, seenParticipant) =>
                message + seenParticipant.firstName + " , ",
              "Seen by "
            );
            list.push({
              _id: msg._id + "_seen",
              type: "seen",
              content: seenMessage.substring(0, seenMessage.length - 3),
              chat: chat._id,
            });
          }
          list.push(msg);
          unsetParticipants = unsetParticipants.filter(
            (unsetParticipant) =>
              !seenParticipants.find(
                (seenParticipant) => seenParticipant._id == unsetParticipant._id
              )
          );
        } else {
          list.push(msg);
        }
      } else {
        list.push(msg);
      }
    });
    list.reverse();
    let typingParticipants = chat.participants.filter(
      (participant) => participant.activity == "typing"
    );
    //console.log("Typing",typingParticipants)
    if (typingParticipants.length != 0) {
      let typingMessage = typingParticipants.reduce(
        (msg, participant) => participant.firstName + " , ",
        ""
      );
      list.push({
        _id: chat._id + "_typing",
        type: "typing",
        content:
          typingMessage.substring(0, typingMessage.length - 3) +
          (typingParticipants.length == 1 ? " is typing" : " are typing"),
        chat: chat._id,
        sender: {
          displayPicSrc: "",
          firstName: "",
          lastName: "",
          email: "",
          _id: chat._id + "_typing",
          userType: "",
        },
      });
      //console.log(list)
    }
    return list;
    //let bakedParticipants=getParticipantsLastSeenMessage(chat,user,messages);
    //return bakeMessagesWithSeenInfo(messages,bakedParticipants);
  };

  const eventHandler = async (event: Event) => {
    let triggerObj: TriggerObject;
    switch (event.name) {
      case "send":
        const formData = new FormData();
        formData.append("content", event.data);
        formData.append("chatId", id);

        try {
          if (event.data.trim()) {
            const response = await communicationMessage(formData);
            setTyping("Ava is typing");

            if (response.data.success) {
              dispatch(updateChat(response.data.data.chat));
              dispatch(addMessage(response.data.data.message));
              setUploadFiles([]);
            } else {
              console.error("Failed to send message or upload files.");
            }
            const response1 = await AVAcommunicationMessage({
              content: event.data,
              chatId: id,
            });
            setTyping("");

            if (response1.data.success) {
              dispatch(updateChat(response1.data.data.chat));
              dispatch(addMessage(response1.data.data.message));
            } else {
              console.error("Failed to send message or upload files.");
            }
          }
        } catch (error) {
          console.error("Error during file upload or message sending:", error);
        }

        break;
      case "startTyping":
        triggerObj = {
          action: "typing",
          sender: { ...profileInfo.data, userType: "student" },
          recievers: chat?.participants.filter(
            (participant) => participant._id != profileInfo.data?._id
          ),
          data: "start",
        };
        //   Socket.emit("trigger", triggerObj);
        break;
      case "stopTyping":
        //   triggerObj = {
        //     action: "typing",
        //     sender: { ...profileInfo.data, userType: "student" },
        //     recievers: chat?.participants.filter((participant) => participant._id != profileInfo.data?._id),
        //     data: "stop"
        //   }
        // setTyping("")

        //   Socket.emit("trigger", triggerObj);
        break;
      default:
        break;
    }
  };

  useEffect(() => {
    //   // if (id) {
    //   Socket.removeListener("trigger", triggerMessages);
    //   Socket.on("trigger", triggerMessages);
    fetchMessage();
    //   return () => { Socket.removeListener("trigger", triggerMessages) }
    // }
  }, []);

  list.current =
    chat && profileInfo.data
      ? bakeMessages([...messages.data], chat, profileInfo.data)
      : [];

  const Header = (props: any) => {
    return (
      <div
        style={{
          background: "#F8F8FC",
          padding: "0.5rem",
          borderRadius: "0.8rem",
          position: "relative",
        }}
      >
        <Stack
          direction="row"
          spacing={2}
          alignItems="center"
          justifyContent="space-between"
        >
          <Box sx={{ display: "flex", gap: "1rem", alignItems: "center" }}>
            <Avatar
              src={
                participantDetails?.role === "Virtual_Assistant"
                  ? Images.ai
                  : participantDetails?.displayPicSrc
              }
              alt="profile"
              sx={{ width: "3rem", height: "3rem", borderRadius: "50%" }}
            />
            <Typography
              fontWeight="500"
              sx={{
                position: "absolute",
                right: "10px",
                top: "-1rem",
                background: "#fff",
                p: 1,
                borderRadius: "10px",
                color:
                  participantDetails?.role === "counsellor"
                    ? "#3B3F76"
                    : participantDetails?.role === "processCoordinator"
                    ? "#569228"
                    : "#FEB853",
                fontSize: "14px",
              }}
            >
              {participantDetails?.role
                ? normalizeType(participantDetails?.role)
                : participantDetails?.role}
            </Typography>
            <Box>
              {participantDetails?.firstName || participantDetails?.lastName ? (
                <Typography fontWeight="500">
                  {setWordCase(participantDetails?.firstName)}
                  &nbsp;{setWordCase(participantDetails?.lastName)}
                </Typography>
              ) : (
                <Typography>User</Typography>
              )}
            </Box>
          </Box>
          <Box sx={{ float: "right" }}>
            <Button
              sx={{ textTransform: "none", color: "#3b3f76", float: "right" }}
              onClick={() =>
                props.eventHandler({
                  name: "block",
                  data: {
                    action: "block",
                    id: participantDetails?._id,
                  },
                })
              }
            >
              {participantDetails &&
                participantDetails?.role !== "Virtual_Assistant" &&
                "Block"}
            </Button>
          </Box>
        </Stack>
      </div>
    );
  };
  const Content = (props: any) => {
    const bottomRef = useRef<HTMLDivElement>(null);
    const chatContainerRef = useRef<HTMLDivElement>(null);
    const [isUserAtBottom, setIsUserAtBottom] = useState(true);

    const handleScroll = () => {
      if (chatContainerRef.current) {
        const { scrollTop, scrollHeight, clientHeight } =
          chatContainerRef.current;
        const isAtBottom = scrollHeight - scrollTop - clientHeight <= 5;
        setIsUserAtBottom(isAtBottom);
      }
    };
    const predefinedMessages = [
      "Top courses in USA?",
      "Best universities in Computer Science ?",
    ];

    useEffect(() => {
      if (isUserAtBottom) {
        bottomRef.current?.scrollIntoView({ behavior: "auto" });
      }
    }, [list.current]);

    useEffect(() => {
      bottomRef.current?.scrollIntoView({ behavior: "auto" });
    }, []);

    return (
      <Box
        ref={chatContainerRef}
        onScroll={handleScroll}
        sx={{
          overflowY: "scroll",
          mt: 3,
          height: "390px",
          "&::-webkit-scrollbar": {
            width: "12px",
          },
          "&::-webkit-scrollbar-thumb": {
            backgroundColor: "transparent",
            borderRadius: "8px",
          },
        }}
      >
        {loading ? (
          <Box
            sx={{
              display: "flex",
              justifyContent: "center",
              alignItems: "center",
              height: "300px",
            }}
          >
            <CircularProgress />
          </Box>
        ) : (
          <div className="chat-container">
            {list.current.length > 0 ? (
              <>
                {list.current.map((message: any) => (
                  <>
                    {message.type !== "seen" && message.type !== "typing" ? (
                      <div
                        key={message._id}
                        className={`message ${
                          message?.sender?._id === profileInfo.data?._id
                            ? "sent"
                            : "received"
                        }`}
                      >
                        <ContentRenderer content={message.content} />
                        {message?.document?.data?.preview_url ? (
                          <iframe
                            src={message?.document?.data?.preview_url}
                            title="preview"
                            style={{
                              border: "none",
                              width: "150px",
                              height: "150px",
                              objectFit: "contain",
                            }}
                          />
                        ) : (
                          ""
                        )}
                        <Avatar
                          src={message.sender?.displayPicSrc}
                          alt=""
                          sx={
                            message?.sender?._id === profileInfo.data?._id
                              ? {
                                  width: "1.5rem",
                                  height: "1.5rem",
                                  borderRadius: "50%",
                                  position: "absolute",
                                  top: "-12px",
                                  right: 0,
                                }
                              : {
                                  width: "1.5rem",
                                  height: "1.5rem",
                                  borderRadius: "50%",
                                  position: "absolute",
                                  top: "-12px",
                                  left: 0,
                                }
                          }
                        />
                      </div>
                    ) : (
                      <>
                        <div className="seenMsg" key={message._id}>
                          {message.content}
                        </div>
                      </>
                    )}
                  </>
                ))}
                {typing && (
                  <Typography
                    sx={{
                      textAlign: "right",
                      fontSize: "13px",
                      color: "#3b3f76",
                      marginTop: "8px",
                    }}
                  >
                    {typing}
                  </Typography>
                )}
                <div ref={bottomRef}></div>
              </>
            ) : (
              <Box
                sx={{ display: "flex", flexDirection: "column", gap: "8px" }}
              >
                <Typography sx={{ fontSize: "14px" }}>
                  Hi, How can I help you ? Here are few questions{" "}
                </Typography>
                {predefinedMessages.map((msg, index) => (
                  <Link
                    key={index}
                    sx={{
                      fontSize: "14px",
                      textDecoration: "none",
                      cursor: "pointer",
                    }}
                    onClick={() =>
                      props.eventHandler({ name: "send", data: msg })
                    }
                  >
                    {msg}
                  </Link>
                ))}
              </Box>
            )}
          </div>
        )}
      </Box>
    );
  };

  const Footer = (props: any) => {
    const [content, setContent] = useState("");
    const [loading, setLoading] = useState(false);
    const fileInputRef = useRef<HTMLInputElement>(null);
    const actionHandler = (action: any) => {
      // console.log('action', action)

      if (action.type == "reply") {
        setRepliedTo(action.data.id);
      }
      if (action.type == "copy") {
        // console.log(messages[action.data.index])
        // navigator.clipboard.writeText(messages[action.data.index].content);
      }
    };

    const showReplyDiv = (messagedata: any) => {
      setReplyDiv(true);
      setReplyDivData(messagedata);
    };
    const handleFileChange = (event: any) => {
      const selectedFiles = Array.from(event.target.files);
      setUploadFiles(selectedFiles as File[]);
      // console.log(selectedFiles)
    };
    const removeFile = (index: any) => {
      const updatedFiles = [...uploadFiles];
      updatedFiles.splice(index, 1);
      setUploadFiles(updatedFiles);
    };

    const handleSend = async () => {
      try {
        if (content.trim() !== "" || (uploadFiles && uploadFiles.length > 0)) {
          setLoading(true);
          await props.eventHandler({ name: "send", data: content });
        } else {
          console.log("No content or files to send.");
        }
      } catch (error) {
        console.error("Error while sending:", error);
      } finally {
        setLoading(false);
      }
    };

    const handleKeyDown = (event: React.KeyboardEvent<HTMLInputElement>) => {
      if (event.key === "Enter" && !event.shiftKey && content.trim() !== "") {
        event.preventDefault();
        handleSend();
      }
    };

    return (
      <>
        {participantDetails && (
          <div style={{ position: "relative" }}>
            {uploadFiles?.length > 0 && (
              <div
                style={{
                  display: "flex",
                  flexDirection: "column",
                  alignItems: "center",
                  position: "absolute",
                  top: "-44px",
                  right: "35%",
                }}
              >
                <div style={{ width: "fitContent" }}>
                  <Typography fontSize="0.9rem">Selected files:</Typography>
                  <ul style={{ listStyle: "none", padding: 0, margin: 0 }}>
                    {uploadFiles.map((file, index) => (
                      <li
                        style={{ fontSize: "12px", display: "flex" }}
                        key={index}
                      >
                        {file.name}
                        <Close
                          onClick={() => removeFile(index)}
                          sx={{ fontSize: "1rem", cursor: "pointer" }}
                        />
                      </li>
                    ))}
                  </ul>
                </div>
              </div>
            )}
            {replyDiv ? (
              <div className="replyDiv">
                <h5>reply to:- {replyDivData}</h5>
              </div>
            ) : (
              ""
            )}
            <Grid
              container
              sx={{
                background: "#F8F8FC",
                padding: "0.5rem",
                borderRadius: "0.8rem",
              }}
            >
              <Grid item xs={9.5}>
                <TextField
                  variant="outlined"
                  size="small"
                  onFocus={() => props.eventHandler({ name: "startTyping" })}
                  onBlur={() => props.eventHandler({ name: "stopTyping" })}
                  multiline
                  // maxRows={3}
                  rows={2}
                  fullWidth
                  placeholder="Type your message..."
                  value={content}
                  sx={{
                    "& .MuiOutlinedInput-notchedOutline": {
                      border: 0,
                      "&:Focus": {
                        border: 0,
                      },
                    },
                    "& .Mui-focused .MuiOutlinedInput-notchedOutline": {
                      border: "none !important",
                    },
                  }}
                  onChange={(e) => setContent(e.target.value)}
                  onKeyDown={handleKeyDown}
                />
              </Grid>
              <Grid
                item
                xs={1.5}
                sx={{
                  display: "flex",
                  justifyContent: "center",
                  alignItems: "center",
                  background: "#fff",
                  borderRadius: "6px",
                }}
              >
                {loading ? (
                  <img
                    src={Images.standardLoader}
                    alt="loader"
                    style={{ width: "1rem" }}
                  />
                ) : (
                  <img
                    src={Images.sendIcon}
                    alt="sendIcon"
                    style={{ width: "2rem", height: "2rem" }}
                    onClick={handleSend}
                  />
                )}
              </Grid>
            </Grid>
          </div>
        )}
      </>
    );
  };

  return (
    <div>
      <Dialog
        open={open}
        sx={{
          right: "0",
          left: { md: "45%", xs: "0", lg: "68%" },
          top: { md: "10px", xs: "0", lg: "25px" },
        }}
        PaperProps={{
          style: {
            borderRadius: "1rem",
            padding: "1rem",
            width: "410px",
            height: "582px",
            // background: "#EDF2F6",
          },
        }}
        TransitionComponent={Transition1}
        keepMounted
        onClose={() => setOpen(false)}
        aria-describedby="alert-dialog-slide-description"
      >
        <Box
          sx={{
            display: "flex",
            alignItems: "center",
            justifyContent: "center",
          }}
        >
          <KeyboardArrowDownIcon
            sx={{
              position: "absolute",
              top: 0,
              color: "#0F0F0F",
              fontSize: "2rem",
              cursor: "pointer",
              display: "flex",
              justifyContent: "center",
              alignItems: "center",
            }}
            onClick={() => setOpen(false)}
          />
        </Box>
        <Box sx={{ mt: 3 }}>
          <Header eventHandler={eventHandler} />
          <Content eventHandler={eventHandler} />
          <Footer eventHandler={eventHandler} />
        </Box>
      </Dialog>
    </div>
  );
};

export default VirtualChatContainer;
