import React, { useRef, useCallback, useEffect, useState } from "react";
import { Box, Typography, Button, Grid } from "@mui/material";
import { useNavigate } from "react-router-dom";
import Images from "src/assets";
import { useAppDispatch, useAppSelector } from "../../../assets/hooks";
import { setPopup } from "src/store/Slices/popupSlice";
import { selectIsAuthenticated, selectRecommendations } from "src/store/selectors";
import "./HowItWorksSection.css";

const HowItWorksSection = () => {
  const scrollRef = useRef<HTMLDivElement | null>(null);
  const scrollByCards = useCallback((direction: 'left' | 'right') => {
    const container = scrollRef.current;
    if (!container) return;
    const amount = Math.min(280, container.clientWidth * 0.8);
    container.scrollBy({ left: direction === 'right' ? amount : -amount, behavior: 'smooth' });
  }, []);
  const navigate = useNavigate();
  const dispatch = useAppDispatch();
  const isAuthenticated = useAppSelector(selectIsAuthenticated);
  const recommendations = useAppSelector(selectRecommendations);
  const [pendingStepId, setPendingStepId] = useState<number | null>(null);

  const openLoginPopup = useCallback(() => {
    dispatch(
      setPopup({
        show: true,
        data: {
          container: {
            name: "login",
            dimensions: { width: "840px", height: "560px", show: "", close: "" },
          },
          type: "custom",
        },
      })
    );
  }, [dispatch]);

  const navigateForStep = useCallback((id: number) => {
    switch (id) {
      case 1:  navigate(`/explore`); break;
      case 2:  navigate(`/profile/personal`); break;
      case 3:  navigate(`/explore`); break;
      case 4:  navigate(`/profile/shortlisted`); break;
      default: break;
    }
  }, [navigate]);

  const handleStepClick = useCallback((id: number) => {
    try {
      if (!isAuthenticated) {
        setPendingStepId(id);
        openLoginPopup();
        return;
      }
      navigateForStep(id);
    } catch {
      setPendingStepId(id);
      openLoginPopup();
    }
  }, [isAuthenticated, openLoginPopup, navigateForStep]);

  useEffect(() => {
    if (isAuthenticated && pendingStepId !== null) {
      navigateForStep(pendingStepId);
      setPendingStepId(null);
    }
  }, [isAuthenticated, pendingStepId, navigateForStep]);

  const steps = [
    { id: 1, title: "Sign up",   description: "Start your journey in seconds. Sign up to explore more.", buttonText: "Start Now",      icon: Images.HowItWorks1 },
    { id: 2, title: "Profile",   description: "Create your profile. Get seen by global universities.",   buttonText: "Create Profile", icon: Images.HowItWorks2 },
    { id: 3, title: "Discover",  description: "Discover top universities. Find your match.",             buttonText: "Discover",       icon: Images.HowItWorks3 },
    { id: 4, title: "Apply",     description: "Apply in minutes. Chase your global dream.",             buttonText: "Start Now",      icon: Images.HowItWorks4 },
  ];

  return (
    <Box className="how-it-works-section">
      {/* Title (same position, improved fluid sizes but visually identical) */}
      <Typography className="how-it-works-title"
        sx={{
          alignSelf: "stretch",
          textAlign: "center",
          color: "white",
          fontFamily: "Neutrif Studio",
          fontWeight: 500,
          wordWrap: "break-word",
        }}
      >
        How it Works?
      </Typography>

      {/* Steps Container  layout unchanged */}
      <Box className="how-it-works-steps-container" ref={scrollRef}>
        {/* Cards  exact structure retained */}
        {steps.map((step) => (
          <Box key={step.id} className="how-it-works-step-card"
            sx={{
              background: "white",
              boxShadow: "0px 0px 4px rgba(0, 0, 0, 0.25)",
              borderRadius: 4,
              flexDirection: "column",
              justifyContent: "flex-start",
              alignItems: "flex-start",
              display: "flex",
              height: "auto",
              position: "relative",
            }}
          >
            {/* Step Number */}
            <Typography className="how-it-works-step-number"
              sx={{
                color: "#5C6163",
                fontFamily: "Neutrif Studio",
                fontWeight: "700",
                opacity: 0.15,
                zIndex: 2,
                lineHeight: 1,
              }}
            >
              {String(step.id).padStart(2, '0')}
            </Typography>

            {/* Content Section */}
            <Box
              sx={{
                alignSelf: "stretch",
                flexDirection: "column",
                justifyContent: "flex-start",
                alignItems: "flex-start",
                gap: 2,
                display: "flex",
                flex: 1,
              }}
            >
              {/* Icon and Title Row */}
              <Box
                sx={{
                  justifyContent: "flex-start",
                  alignItems: "center",
                  display: "inline-flex",
                  gap: 1.5
                }}
              >
                {/* Icon Container */}
                <Box className="how-it-works-step-icon-container"
                  sx={{
                    position: "relative",
                    flexShrink: 0
                  }}
                >
                  <Box className="how-it-works-step-icon-bg"
                    sx={{
                      left: 0,
                      top: 0,
                      position: "absolute",
                      background: "white",
                      borderRadius: 4,
                    }}
                  />
                  <Box
                    component="img"
                    className="how-it-works-step-icon-img"
                    sx={{
                      position: "absolute",
                      objectFit: "contain"
                    }}
                    src={step.icon}
                    alt={step.title}
                    loading="lazy"
                    decoding="async"
                  />
                </Box>

                {/* Title */}
                <Typography className="how-it-works-step-title"
                  sx={{
                    mb: 1,
                    color: "#495052",
                    fontFamily: "Neutrif Studio",
                    fontWeight: 500,
                    wordWrap: "break-word",
                  }}
                >
                  {step.title}
                </Typography>
              </Box>

              {/* Description */}
              <Typography className="how-it-works-step-description"
                sx={{
                  width: "100%",
                  color: "#5C6163",
                  fontFamily: "Neutrif Studio",
                  fontWeight: 400,
                  wordWrap: "break-word",
                }}
              >
                {step.description}
              </Typography>
            </Box>

            {/* Button and Arrow Section */}
            <Box className="how-it-works-step-actions"
              sx={{
                justifyContent: "flex-start",
                alignItems: "center",
                display: "flex",
                width: "100%",
              }}
            >
              {/* Button Text */}
              <Typography className="how-it-works-step-button"
                sx={{
                  flex: 1,
                  justifyContent: "flex-end",
                  display: "flex",
                  flexDirection: "column",
                  color: "rgba(92, 97, 99, 0.40)",
                  fontFamily: "Neutrif Studio",
                  fontWeight: 500,
                  wordWrap: "break-word",
                  cursor: "pointer",
                }}
                onClick={() => handleStepClick(step.id)}
              >
                {step.buttonText}
              </Typography>

              {/* Arrow Icon (hidden) */}
              <Box
                sx={{
                  width: 30,
                  height: 30,
                  padding: 2,
                  opacity: 0,
                  background: "#F48438",
                  borderRadius: 1,
                  flexDirection: "column",
                  justifyContent: "center",
                  alignItems: "center",
                  gap: 1,
                  display: "inline-flex",
                }}
              />
            </Box>
          </Box>
        ))}
      </Box>
    </Box>
  );
};

export default HowItWorksSection;
