import React, { useState } from "react";
import { Box, Typography, Button, Grid } from "@mui/material";
import Images from "src/assets";
import { useAppDispatch, useAppSelector } from "src/assets/hooks";
import { setPopup } from "src/store/Slices/popupSlice";
import VirtualChatContainer from "src/pages/chats/virtualChatContainer";
import { Chat } from "src/types/types";

const AvaChatbotSection = () => {
  const [open1, setOpen1] = useState(false);
  const dispatch = useAppDispatch();
  const userAuthStatus = useAppSelector((state) => state.userAuthStatus);
  const advisorchats = useAppSelector((state) => state.chats);

  const findVirtualAssistant = (advisorArray: Chat[]) => {
    for (const advisor of advisorArray) {
      const participant = advisor?.participants?.find(
        (p: any) => p.role === "Virtual_Assistant"
      );
      if (participant) {
        return advisor;
      }
    }
    return null;
  };

  const virtualAdvisor = findVirtualAssistant(advisorchats?.data?.advisors);

  const handleGetStarted = () => {
    if (userAuthStatus.data?.isAuthorized) {
      setOpen1(true);
    } else {
      dispatch(
        setPopup({
          show: true,
          data: {
            container: {
              name: "login",
              dimensions: {
                width: "800px",
                height: "70%",
                show: "none",
              },
            },
            type: "custom",
          },
        })
      );
    }
  };

  return (
    <Box
      sx={{
        background: "#F5EEE4", // Light beige background
        py: { xs: 6, lg: 10 },
        mb:15
      }}
    >
      <Box
        sx={{
          maxWidth: "1440px",
          width: "100%",
          mx: "auto",
          px: { xs: 2, md: 4, lg: 6 }
        }}
      >
        <Grid container spacing={4} alignItems="center">
          {/* Left Section - Text and CTA */}
          <Grid item xs={12} md={6}>
            <Typography
              variant="h3"
              sx={{
                fontSize: { xs: "1.65rem", md: "1.8rem", lg: "2rem" },
                fontWeight: 390,
                color: "#495052",
                mb: 3,
                lineHeight: 1.2
              }}
            >
              That 3am question about studying abroad shouldn't keep you up at night {" "}
              <Box
                component="span"
                sx={{
                  color: "#F2A05E",
                  fontWeight: 700
                }}
              >
                AVA's
              </Box>{" "}
              here to guide you anytime
            </Typography>
            
            <Button
              variant="contained"
              onClick={handleGetStarted}
              sx={{
                background: "#F48438",
                color: "white",
                borderRadius: "25px",
                px: 4,
                py: 2,
                fontSize: "1.125rem",
                fontWeight: 600,
                textTransform: "none",
                boxShadow: "0 4px 20px rgba(255, 107, 53, 0.3)",
                "&:hover": {
                  background: "#F48438",
                  boxShadow: "0 6px 25px rgba(255, 107, 53, 0.4)"
                }
              }}
            >
              Get Started
            </Button>
          </Grid>

          {/* Right Section - AVA Illustration */}
          <Grid item xs={12} md={6}>
            <Box
              sx={{
                position: "relative",
                height: "150px",
                overflow: "visible"
              }}
            >
             <Box
  component="img"
  src={Images.HpAvaIllu}
  alt="AVA Chatbot Illustration"
  sx={{
    position: "absolute",
    top: "30%",
    left: { xs: "65%", lg: "75%" }, //  Works with sx prop
    transform: "translate(-50%, -50%)",
    height: {xs:"300px",lg:"430px"},
    objectFit: "contain",
    zIndex: 2
  }}
/>
            </Box>
          </Grid>
        </Grid>
      </Box>
      {userAuthStatus.data?.isAuthorized && open1 ? (
        <VirtualChatContainer
          open={open1}
          setOpen={setOpen1}
          id={virtualAdvisor?._id}
          type="advisors"
        />
      ) : null}
    </Box>
  );
};

export default AvaChatbotSection;
